input[type="color"] {
	-webkit-appearance: none;
  border: none;
  width: 35px;
  height: 35px;
  margin: 8%;
  margin: 0% 2% 5% 0%;
  border-radius: 50%;
  border: 1px solid;
  padding: 2px;
  line-height: 1.42857;
  background-image: repeating-linear-gradient(to right,
  #fdfdfd 2px);
  /* box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19); */
  line-height: 1.42857;
}

input[type="color"]::-webkit-color-swatch-wrapper {
  padding: 0;
  border-radius: 50%;
}

input[type="color"]::-webkit-color-swatch {
  border: none;
  border-radius: 50%;
}

#color-picker-img {
  height: 25px;
  width: 25px;
  object-fit: cover;
  position: absolute;
}

#color-picker { 
  /* removes default styling from input color element */
  padding: 0;
  border: none; 
  /* makes input (color swatch) 100% size of container */
  /* position: absolute; */
  width: 25px; 
  height: 25px;
  mix-blend-mode: hue;
  cursor: pointer;
}

/* removes default styling from input color element */
::-webkit-color-swatch {
  border: none;
}

::-webkit-color-swatch-wrapper {
  padding: 0;
}

::-moz-color-swatch,
::-moz-focus-inner {
  border: none;
}

::-moz-focus-inner {
  padding: 0;
}

.colors-container{
  padding:10% 10% 2% 10%;
  display: -webkit-inline-box;
  display: flex;
  align-items: center;
}

.colors{
  width: -webkit-fill-available;
}

#color{
  margin-left: 2%;
}

.add-to-favorites-lable {
  margin-top: 3.5%;
}

.color-picker-container{
  width: 25px;
  height: 25px;
}

@media only screen and (max-height: 320px) {
  .submit-message-button {
    visibility: hidden;
  }
}
#message-icon {
    display: flex;
    justify-content: center;
    padding-bottom: 3%;
}

#send-message-text {
